<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Situación de Ventas
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="12"
      >
        <form @submit.prevent="guardarVenta()">
          <v-row>
            <v-col
              cols="12"
              md="3"
            >
              <v-text-field
                v-model="sale.fecha"
                label="Fecha"
                outlined
                prepend-icon="mdi-calendar"
                required
                disabled
              />
            </v-col>

            <v-col
              cols="12"
              md="5"
            >
              <v-text-field
                v-model="sale.nroComprobante"
                label="Nro Comprobante"
                outlined
                prepend-icon="mdi-label"
                required
                disabled
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-select
                v-model="sale.situacion"
                label="Situación"
                :items="opciones"
                prepend-icon="mdi-list-status"
                outlined
                required
                disabled
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="12"
              md="12"
            >
              <base-material-card
                icon="mdi-account"
                title="Cliente"
                class="px-5 py-3"
              >
                <v-divider class="my-5" />
                <v-row>
                  <v-col
                    cols="12"
                    md="4"
                  >
                    <v-text-field
                      v-model="dni.customerDni"
                      label="DNI"
                      outlined
                      prepend-icon="mdi-barcode"
                      required
                      disabled
                    />
                  </v-col>

                  <v-col
                    cols="12"
                    md="2"
                  >
                    <v-btn
                      color="green"
                      class="mr-4"
                      disabled
                    >
                      <v-icon left>
                        mdi-magnify
                      </v-icon>
                      Buscar
                    </v-btn>
                  </v-col>

                  <v-col
                    cols="12"
                    md="5"
                  >
                    <v-text-field
                      v-model="cliente.name"
                      label="Nombre Cliente"
                      outlined
                      prepend-icon="mdi-account"
                      disabled
                    />
                  </v-col>
                </v-row>
              </base-material-card>
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="12"
              md="12"
            >
              <base-material-card
                icon="mdi-account-box"
                title="Vendedor"
                class="px-5 py-3"
                color="purple"
              >
                <v-divider class="my-5" />
                <v-row>
                  <v-col
                    cols="12"
                    md="12"
                  >
                    <v-select
                      v-model="vendedorSeleccionado"
                      label="Vendedor"
                      :items="vendedor"
                      item-text="name"
                      name="vendedorSeleccionado"
                      outlined
                      prepend-icon="mdi-account"
                      required
                      disabled
                    />
                  </v-col>
                </v-row>
              </base-material-card>
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="12"
              md="12"
            >
              <base-material-card
                icon="mdi-bank"
                title="Financiamiento"
                class="px-5 py-3"
                color="red"
              >
                <v-divider class="my-5" />
                <v-row>
                  <v-col
                    cols="12"
                    md="12"
                  >
                    <v-select
                      v-model="tipoFinanciamiento"
                      label="Tipo Financiamiento"
                      :items="financiamiento"
                      item-text="type"
                      name="tipoFinanciamiento"
                      outlined
                      prepend-icon="mdi-account-cash"
                      required
                      disabled
                    />
                  </v-col>

                  <v-col
                    cols="12"
                    md="12"
                  >
                    <v-select
                      v-model="sale.entity"
                      label="Entidad Bancaria"
                      :items="bancos"
                      outlined
                      prepend-icon="mdi-bank"
                      required
                      disabled
                    />
                  </v-col>

                  <v-col
                    cols="12"
                    md="12"
                  >
                    <v-select
                      v-model="sale.support"
                      label="Sustento"
                      :items="sustentos"
                      outlined
                      prepend-icon="mdi-file"
                      required
                      disabled
                    />
                  </v-col>

                  <v-col
                    cols="12"
                    md="12"
                  >
                    <v-text-field
                      v-model="sale.office"
                      label="Oficina"
                      outlined
                      prepend-icon="mdi-domain"
                      required
                      disabled
                    />
                  </v-col>

                  <v-col
                    cols="12"
                    md="12"
                  >
                    <v-select
                      v-model="sale.account_executive"
                      label="Sectorista"
                      :items="sectoristas"
                      outlined
                      prepend-icon="mdi-account"
                      required
                      disabled
                    />
                  </v-col>
                </v-row>
              </base-material-card>
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="12"
              md="12"
            >
              <base-material-card
                icon="mdi-car-sports"
                title="Vehículo"
                class="px-5 py-3"
                color="blue"
              >
                <v-divider class="my-5" />
                <v-row>
                  <v-col
                    cols="12"
                    md="4"
                  >
                    <v-text-field
                      v-model="carro.serie"
                      label="SERIE TDP"
                      outlined
                      prepend-icon="mdi-barcode"
                      required
                      disabled
                    />
                  </v-col>

                  <v-col
                    cols="12"
                    md="2"
                  >
                    <v-btn
                      color="blue"
                      class="mr-4"
                      disabled
                    >
                      <v-icon left>
                        mdi-magnify
                      </v-icon>
                      Buscar
                    </v-btn>
                  </v-col>

                  <v-col
                    cols="12"
                    md="5"
                  >
                    <v-text-field
                      v-model="vehicle.model"
                      label="Modelo"
                      outlined
                      prepend-icon="mdi-car-sports"
                      disabled
                    />
                  </v-col>
                </v-row>
              </base-material-card>
            </v-col>
          </v-row>
          <v-card-actions>
            <v-btn
              to="/adv/ventas"
              color="green"
              class="mr-4"
            >
              Cerrar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
  import axios from 'axios'

  const urlBuscarDni = 'https://sistema-adv.herokuapp.com/api/customer/find'
  const urlGetVendedores = 'https://sistema-adv.herokuapp.com/api/seller/'
  const urlGetFinanciamiento = 'https://sistema-adv.herokuapp.com/api/financing/'
  const urlBuscarVehiculo = 'https://sistema-adv.herokuapp.com/api/vehicles/find/'
  const urlVenta = 'https://sistema-adv.herokuapp.com/api/sale/'
  var uuid = require('uuid')

  export default {
    name: 'EditarVenta',
    data () {
      return {
        id: null,
        vendedorSeleccionado: {},
        vendedor: [],
        opciones: [
          'Cancelado',
          'Por Desembolsar',
          'Libre',
          'En Espera',
        ],
        sale: {
          fecha: '',
          nroComprobante: '',
          seller: '',
          vehicle: '',
          financing: '',
          entity: '',
          support: '',
          office: '',
          account_executive: '',
          situacion: '',
          customer: '',
        },
        cliente: {
          name: '',
          lastname: '',
          document: '',
          cellphone: '',
          email: '',
          address: '',
        },
        dni: {
          customerDni: '',
        },
        tipoFinanciamiento: {},
        financiamiento: [],
        entidad: '',
        bancos: ['BCP', 'BBVA', 'Interbank', 'ScotiaBank', 'MAF', 'Compartamos'],
        sustentos: ['Boleta de Venta', 'Factura de Venta', 'Carta de Aprobación'],
        sectoristas: ['Oscar Vera', 'OTRO'],
        carro: {
          serie: '',
        },
        vehicle: {
          brand: '',
          cod_tdp: '',
          serie_tdp: '',
          category: '',
          model: '',
          colour: '',
          manufacturing_year: '',
          model_year: '',
          brand_office: '',
          location: '',
          price: '',
          status: '',
        },
      }
    },
    mounted () {
      this.ObtenerVendedores()
      this.ObtenerFinanciamiento()
      this.activarEdicion()
    },
    methods: {
      activarEdicion () {
        this.id = this.$route.params.id
        axios.get(urlVenta + this.id).then(res => {
          // console.log(res.data);
          this.sale = res.data
          this.tipoFinanciamiento = this.sale.financing.type
          this.dni.customerDni = this.sale.customer.document
          this.cliente = this.sale.customer
          this.vendedorSeleccionado = this.sale.seller.name
          this.carro.serie = this.sale.vehicle.serie_tdp
          this.vehicle = this.sale.vehicle
        })
      },
      ObtenerVendedores () {
        axios
          .get(urlGetVendedores)
          .then((res) => {
            this.vendedor = res.data
          })
          .catch((e) => {
            console.log(e)
          })
      },
      ObtenerFinanciamiento () {
        axios.get(urlGetFinanciamiento).then((res) => {
          this.financiamiento = res.data
        }).catch(e => { console.log(e) })
      },
      guardarVenta () {
        // Mandar campos al objeto sale
        this.sale.seller = this.vendedorSeleccionado
        this.sale.customer = this.cliente.document
        this.sale.financing = this.tipoFinanciamiento
        this.sale.vehicle = this.vehicle.serie_tdp

        const router = this.$router
        const params = this.sale

        // console.log(params);
        axios.put(urlVenta + this.id, params).then((res) => {
          router.push('/adv/ventas')
        }).catch(e => { console.log(e) })
      },
    },
  }
</script>
